import React, { Fragment } from 'react'
import { inject, observer } from 'mobx-react'
import { compose, lifecycle, withHandlers } from 'recompose'
import { map, pipe, find, get } from 'lodash/fp'

// @ts-ignore
import { headerPlayerParams } from 'config/params'
// @ts-ignore
import { getSummaryStatValue } from 'module/utils/helpers/getSummaryStatValue'
// @ts-ignore
import Loader from 'features/components/common/loader/Loader'

// @ts-ignore
import { HeaderStatsItem } from 'features/HeaderStatsItem'

type TProps = {
  getStat: (param_id: number) => number | string,
  getEvents: (param_id: number) => Array<number>,
  playerProfile: {
    profile: {
      amplua: { id: number },
    },
  },
  playerStats: {
    isFetching: boolean,
    stats: Array<any>,
  },
};

/**
 * Отображение блока со статистикой в шапке профиля
 */
export const PlayerStats = ({
  getEvents,
  getStat,
  playerProfile: { profile },
  playerStats: { isFetching },
}: TProps) => (
  <div className='player-card-stats-list'>
    {isFetching ? (
      <Loader />
    ) : (
      <Fragment>
        {map(({ lexic_id, param_id, percent }) => (
          //@ts-ignore
          <HeaderStatsItem
            key={param_id}
            paramId={param_id}
            lexicId={lexic_id}
            percent={percent}
            events={getEvents(param_id)}
            statValue={getStat(param_id)}
          />
              //@ts-ignore
        ))(headerPlayerParams[profile.amplua.id])}
      </Fragment>
    )}
  </div>
)

const PlayerStatsComposed = compose<any, TProps>(
  inject('playerProfile', 'playerStats', 'selectedMatches'),
  //@ts-ignore
  withHandlers({
    getStat: ({ playerStats: { stats } }) => (param_id: number) =>
      getSummaryStatValue(param_id, stats, 'average'),

    getEvents: ({ playerStats: { stats } }) => (param_id: number) =>
      pipe(
        find({ f_param: param_id }),
        get('f_match_event_ids_json'),
      )(stats) || [],
  }),
  lifecycle({
    componentDidMount() {
      const {
        //@ts-ignore
        page,
        //@ts-ignore
        pageId,
        //@ts-ignore
        playerProfile: {
          profile: {
            amplua: { id },
          },
        },
        //@ts-ignore
        playerStats: { fetch },
        //@ts-ignore
        selectedMatches,
      } = this.props
      fetch(pageId, selectedMatches[page][pageId], id)
    },
  }),
  observer,
)(PlayerStats)

export default PlayerStatsComposed
