import { observer, inject } from 'mobx-react'
import get from 'lodash/get'
import filter from 'lodash/filter'
import find from 'lodash/find'

import { compose, withProps, withHandlers } from 'recompose'

// @ts-ignore
import getMatchLinksHoc from 'hocs/getMatchLinksHoc'
// @ts-ignore
import getExtLinkHOC from 'hocs/getExtLinkHOC'
// @ts-ignore
import { clickableClassNameHOC } from 'hocs/getClickableTdClassNameHoc'

export const trComposed = compose(
  inject('videoLink', 'lexics', 'shootsRange', 'matchLinks', 'matches'),
  getMatchLinksHoc,
  getExtLinkHOC,
  observer,
  //@ts-ignore
  withProps(({ matches: { list }, pageId, rowInfo: { f_match } }) => {
    //@ts-ignore
    const checkMatchId = stat => stat.f_match === f_match
    return {
      rowInfo: find(get(list, `${pageId}.matches`, []), checkMatchId),
    }
  }),
  clickableClassNameHOC,
  //@ts-ignore
  withHandlers({
    // Фильтруем всю статистику только по конкретной команде
    getStatsListByTeam: ({ tableStats }: { tableStats: Array<any> | void }) => (
      teamId?: number,
      matchId?: number,
    ) =>
      //@ts-ignore
      filter(tableStats, { f_team: teamId, f_match: matchId }),
    // Фильтруем всю статистику только по конкретному матчу для строки
    getStatsListByPlayer: ({ tableStats }) => (matchId: number) =>
      filter(tableStats, { f_match: matchId }),
  }),
  observer,
)
