import React from 'react'
import { Link } from 'react-router-dom'

import map from 'lodash/map'

// @ts-ignore
import { getStatValue } from 'module/utils/helpers/getStatValue'

import { TrMatchComposed } from './composed'

type TProps = {
  activeId: string,
  getClassName: (param: {}, activeId: string) => string,
  getCurrentPlayerStats: (f_player: number) => Array<any>,
  getExtLink: (param: {}, f_player: number, playerName: string) => void,
  getPlayerName: (f_player: number, lang: string) => string,
  lexics: { lang: string },
  rowInfo: {
    f_player: number,
    num: number,
  },
  selectedParams: Array<{}>,
}

// Строка со статистикой по матчу
const TrMatchComponent = ({
  activeId,
  getClassName,
  getCurrentPlayerStats,
  getExtLink,
  getPlayerName,
  lexics: { lang },
  rowInfo: { f_player, num },
  selectedParams,
}: TProps) => (
  <tr className='stats-table__table-row'>
    <td>
      <Link target='_blank' to={`/players/${f_player}`}>
        <div className='stats-cell'>
          <div className='player-number'>{num}</div>
          <div className='player-name'>{getPlayerName(f_player, lang)}</div>
        </div>
      </Link>
    </td>
    {map(selectedParams, param => (
      <td
        //@ts-ignore
        key={`${param.param_id}_${param.gear_group_id}`}
        className={getClassName(param, activeId)}
      >
        <table>
          <tbody>
            <tr
              //@ts-ignore
              onClick={getExtLink(
                param,
                f_player,
                getPlayerName(f_player, lang),
              )}
            >
              <td>{getStatValue(param, getCurrentPlayerStats(f_player))}</td>
            </tr>
          </tbody>
        </table>
      </td>
    ))}
  </tr>
)

export const TrMatch = TrMatchComposed(TrMatchComponent)
