
import { inject, observer } from 'mobx-react'
import { compose, withHandlers } from 'recompose'
import get from 'lodash/get'
import find from 'lodash/find'
import filter from 'lodash/filter'
import isEmpty from 'lodash/isEmpty'

// @ts-ignore
import { getLongSuffix } from 'module/utils/helpers'
// @ts-ignore
import { clickableClassNameHOC } from 'hocs/getClickableTdClassNameHoc'

type TProps = {
  videoLink: {
    request: (boby: {}) => void,
  },
  lexics: {
    lang: string,
  },
  shootsRange: {},
  matchTable: {},
  matchProfile: {},
  tableStats: Array<any>,
};

export const TrMatchComposed = compose<any, TProps>(
  inject('videoLink', 'lexics', 'shootsRange', 'matchTable', 'matchProfile'),
  //@ts-ignore
  withHandlers({
    /**
     * Получение списка параметров для конкретного игрока
     * @param {array} tableStats - весь список параметров
     * @param {number} f_player - id игрока
     */
    //@ts-ignore
    getCurrentPlayerStats: ({ tableStats }) => playerId =>
      filter(tableStats, ({ f_player }) => f_player === playerId),
    /**
     * Получение информации об игроке
     * @param {number} f_player - id игрока
     * @param {string} lang - язык из настроек
     */
    getPlayerName: ({
      matchTable: { playersInfo },
      //@ts-ignore
      //@ts-ignore
    }) => (playerId, lang) => {
      const player = get(
        find(playersInfo, { f_player: playerId }),
        'prop_player[0]',
      )
      const suffix = getLongSuffix(lang)
      const firstName = player[`firstname_${suffix}`]
      const lastName = player[`lastname_${suffix}`]

      return `${firstName} ${lastName}`
    },
    /**
     * Функция открытия окна с видео
     * @param {number} param_id - id параметра для поиска в таблице со статистикой
     * @param {number} f_player - id игрока
     */
    getExtLink: ({
      lexics: { lang },
      shootsRange,
      tableStats,
      videoLink: { request },
    }) => (
      //@ts-ignore
      //@ts-ignore
      { lexical_formula, param_id },
      //@ts-ignore
      playerId,
      //@ts-ignore
      playerName,
    ) => () => {
      const events = get(
        find(tableStats, { f_param: param_id, f_player: playerId }),
        'f_match_event_ids_json',
        [],
      )

      if (!isEmpty(events)) {
        request({
          events,
          range: shootsRange,
          lang,
          lexical_formula,
          playerInfo: playerName,
          type: 'matches',
        })
      }
    },
  }),
  clickableClassNameHOC,
  observer,
)
