import { inject, observer } from 'mobx-react'
import filter from 'lodash/filter'
import {
  compose,
  withProps,
  withHandlers,

} from 'recompose'

import getMatchLinksHoc from 'hocs/getMatchLinksHoc'
import getExtLinkHOC from 'hocs/getExtLinkHOC'
import { clickableClassNameHOC } from 'hocs/getClickableTdClassNameHoc'

import { TPropsTrComposed } from './types'

export const TrComposed = compose<any, TPropsTrComposed>(
  inject(
    'videoLink',
    'lexics',
    'shootsRange',
    'matchLinks',
  ),
  getMatchLinksHoc,
  getExtLinkHOC,
  observer,
  //@ts-ignore
  withProps(({
    rowInfo: {
      f_match,
      opp_team_id,
      team_id,
    },
    tableStats,
  }) => {
    //@ts-ignore
    const checkMatchId = stat => stat.f_match === f_match
    return ({
      teams: [team_id, opp_team_id],
      tableStats: filter(tableStats, checkMatchId),
    })
  }),
  clickableClassNameHOC,
  withHandlers({
    // Фильтруем всю статистику только по конкретной команде
    getStatsListByTeam: (
      { tableStats }: { tableStats: Array<any> | void },
      //@ts-ignore
    ) => (teamId?: number | null, matchId?: number) => {
      let filterOptions = { f_match: matchId }
      if (teamId) {
        filterOptions = {
          ...filterOptions,
          //@ts-ignore
          f_team: teamId,
        }
      }
      //@ts-ignore
      return filter(tableStats, filterOptions)
    },
  }),
  observer,
)
