import React, { Fragment } from 'react'
import { inject, observer } from 'mobx-react'
import { toJS } from 'mobx'

import {
  compose,
  lifecycle,
  withHandlers,
} from 'recompose'
import map from 'lodash/fp/map'
import reduce from 'lodash/fp/reduce'
import pipe from 'lodash/fp/pipe'
import flatten from 'lodash//fp/flatten'

import { team_params } from 'config/params'
import { getSummaryStatValue } from 'module/utils/helpers/getSummaryStatValue'

import Loader from 'features/components/common/loader/Loader'
import { HeaderStatsItem } from 'features/HeaderStatsItem'

import {
  TComposedInner,
  TPropsOuter,
  TWithHandlers,
  TWithHandlersOuter,
  TReduceResult,
  TParam,
  TLifecycleProps,
} from './types'


/**
 * Отображение блока со статистикой в шапке профиля
 */
export const TeamStatsComponent = ({
  getEvents,
  getStat,
  teamProfile: { profile },
  teamStats: { isFetching },
}: TComposedInner) => (
  <div className='player-card-stats-list'>
    {isFetching ? (
      <Loader />
    ) : (
      <Fragment>
        {profile
          && map(({ lexic_id, param_id, percent }) => (
            <HeaderStatsItem
              key={param_id}
              paramId={param_id}
              lexicId={lexic_id}
              percent={percent}
              events={getEvents(param_id)}
              statValue={getStat(param_id)}
              profile={profile}
            />
          ))(team_params)}
      </Fragment>
    )}
  </div>
)

const teamStatsComposed = compose<TComposedInner, TPropsOuter>(
  inject(
    'teamProfile',
    'teamStats',
    'selectedMatches',
  ),
  withHandlers<TWithHandlers, TWithHandlersOuter>({
    getEvents: ({ teamStats: { stats } }) => (param_id: number) =>
      pipe(
        reduce((acc: TReduceResult, curr: TParam) => {
          if (curr.f_param === param_id && curr.f_match_event_ids_json) {
            acc.push(curr.f_match_event_ids_json)
          }
          return acc
        }, []),
        flatten,
      )(stats) || [],

    getStat: ({ teamStats: { stats } }) => (param_id: number) =>
      getSummaryStatValue(param_id, stats, 'average'),
  }),
  lifecycle<TLifecycleProps, {}>({
    componentDidMount() {
      const {
        page,
        pageId,
        selectedMatches,
        teamProfile: {
          profile: { id },
        },
        teamStats: { fetch },
      } = this.props
      /**
       * Получение данных по выбранным матчам.
       * @param {number} id - id профиля команды
       * @param {array} selectedMatches[page][pageId] - id матчей для запроса
       */
      fetch(id, toJS(selectedMatches[page][pageId]))
    },
  }),
  observer,
)

export const TeamStats = teamStatsComposed(TeamStatsComponent)
