import React from 'react'
import map from 'lodash/map'

// @ts-ignore
import { playListTypes } from 'config/playListTypes'
// @ts-ignore
import { getStatValue } from 'module/utils/helpers/getStatValue'

import { trComposed } from './composed'
import { LeftSideTr } from '../LeftSideTr'
import { TPropsTr } from './types'

// Строка со статистикой по матчу
// @ts-ignore
export const Tr = trComposed(
  //@ts-ignore
  ({
    activeId,
    getClassName,
    getExtLink,
    getPost,
    getStatsListByPlayer,
    getStatsListByTeam,
    lexics: { page },
    matchLinks: { isFetching, links },
    matchPopupOpened,
    pageId,
    rowInfo,
    rowInfo: { f_match, f_team_opponent },
    selectedParams,
    showOpponentStats,
    toggleClose,
  }: TPropsTr) => (
    <tr className='stats-table__table-row'>
      {/*
      //@ts-ignore */}
      <LeftSideTr
        showOpponentStats={showOpponentStats}
        rowInfo={rowInfo}
        pageId={pageId}
        handleClick={getPost}
        matchPopupOpened={matchPopupOpened}
        isFetching={isFetching}
        links={links}
        toggleClose={toggleClose}
      />
      {map(selectedParams, param => (
        <td
          //@ts-ignore
          key={`${param.param_id}_${param.gear_group_id}`}
            //@ts-ignore
          className={getClassName(param, activeId)}
        >
          <table>
            <tbody>
              <tr
                //@ts-ignore
                onClick={getExtLink({
                  param,
                  id: +pageId,
                  match: rowInfo,
                  filterType: page,
                  playListType: playListTypes.matches,
                })}
              >
                <td>
                  {page === 'team'
                      //@ts-ignore
                      && getStatValue(param, getStatsListByTeam(+pageId, f_match))}
                  {page === 'player'
                      //@ts-ignore
                      && getStatValue(param, getStatsListByPlayer(f_match))}
                </td>
              </tr>
            </tbody>
          </table>
          {showOpponentStats && (
            <table>
              <tbody>
                <tr
                  //@ts-ignore
                  onClick={getExtLink({
                    param,
                    id: f_team_opponent,
                    match: rowInfo,
                    filterType: page,
                    playListType: playListTypes.matches,
                  })}
                >
                  <td>
                    {getStatValue(
                      //@ts-ignore
                      param,
                      getStatsListByTeam(f_team_opponent, f_match),
                    )}
                  </td>
                </tr>
              </tbody>
            </table>
          )}
        </td>
      ))}
    </tr>
  ),
)
