import React from 'react'
import map from 'lodash/map'

// @ts-ignore
import { playListTypes } from 'config/playListTypes'
// @ts-ignore
import { getStatValue } from 'module/utils/helpers/getStatValue'
// @ts-ignore
import { getSummaryStatValue } from 'module/utils/helpers/getSummaryStatValue'

import { LeftSideTr } from '../LeftSideTr'
import { TrComposed } from './composed'
import { TPropsTr } from './types'

// Строка со статистикой по матчу
export const Tr = TrComposed(
  ({
    activeId,
    getClassName,
    getExtLink,
    getPost,
    getStatsListByTeam,
    matchLinks: { isFetching, links },
    matchPopupOpened,
    pageId,
    rowInfo,
    rowInfo: { f_match },
    selectedParams,
    showOpponentStats,
    teams,
    toggleClose,
  }: TPropsTr) => (
    <tr className='stats-table__table-row'>
      {/*
        //@ts-ignore */}
      <LeftSideTr
        showOpponentStats={showOpponentStats}
        rowInfo={rowInfo}
        pageId={pageId}
        handleClick={getPost}
        matchPopupOpened={matchPopupOpened}
        isFetching={isFetching}
        links={links}
        toggleClose={toggleClose}
      />
      {map(selectedParams, param => (
        <td
          key={`${param.param_id}_${param.gear_group_id}`}
            //@ts-ignore
          className={getClassName(param, activeId)}
        >
          {showOpponentStats ? (
            <table>
              <tbody>
                {map(teams, team => (
                  <tr
                    key={team}
                      //@ts-ignore
                    onClick={getExtLink({
                      param,
                      id: team,
                      match: rowInfo,
                      filterType: 'team',
                      playListType: playListTypes.tournamentMatches,
                    })}
                  >
                    <td>
                      {getStatValue(param, getStatsListByTeam(team, f_match))}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          ) : (
            <table>
              <tbody>
                <tr
                  //@ts-ignore
                  onClick={getExtLink({
                    param,
                    id: +pageId,
                    match: rowInfo,
                    playListType: playListTypes.tournamentMatches,
                  })}
                >
                  <td>
                    {getSummaryStatValue(
                      param.param_id,
                      //@ts-ignore
                      getStatsListByTeam(null, f_match),
                      'total',
                    )}
                  </td>
                </tr>
              </tbody>
            </table>
          )}
        </td>
      ))}
    </tr>
  ),
)
