import { observer } from 'mobx-react'
import { compose, withHandlers } from 'recompose'

import orderBy from 'lodash/orderBy'
// @ts-ignore
import { TBody as TBodyComponent } from 'features/Table'

export const TBody = compose(
  //@ts-ignore
  withHandlers({
    /**
     * Получение списка матчей или игроков, отсортированного по значению выбранного параметра
     * в таблице
     * @param {boolean} sortInc - флаг для сортировки (по убыванию или по возрастанию)
     */
    getSortedList: ({ rows }) => (sortInc: boolean) => {
      const sortType = sortInc ? 'acs' : 'desc'
      //@ts-ignore
      //@ts-ignore
      return orderBy(rows, 'sortBy', sortType)
    },
  }),
  observer,
  //@ts-ignore
)(TBodyComponent)
