import { compose, withHandlers } from 'recompose'
import isEmpty from 'lodash/isEmpty'
import filter from 'lodash/filter'
import reduce from 'lodash/reduce'

import { inject } from 'mobx-react'

const getExtLinkHOC = compose(
  inject('lexics', 'videoLink', 'shootsRange'),
  withHandlers({
    getFilterCondition: () => (
      stat: { f_match: number, f_param: number },
      filterType?: string,
      //@ts-ignore
      id: number,
    ) => {
      // Проверяем дополнительно по команде или игроку, если необходимо
      //@ts-ignore
      const checkByTeam = filterType ? stat[`f_${filterType}`] === id : true
      return checkByTeam
    },
  }),
  //@ts-ignore
  withHandlers({
    getExtLink: ({
      getFilterCondition,
      lexics: { lang },
      shootsRange,
      tableStats,
      videoLink: { request },
    }) => ({
      //@ts-ignore
      //@ts-ignore
      filterType,
      //@ts-ignore
      id,
      //@ts-ignore
      match,
      //@ts-ignore
      param: { lexical_formula, param_id },
      //@ts-ignore
      playListType,
    }) => () => {
      const matchTeams = filter(
        tableStats,
        stat =>
          stat.f_match === match.f_match
          && stat.f_param === param_id
          && getFilterCondition(stat, filterType, id),
      )

      const events = reduce(
        matchTeams,
        (acc, { f_match_event_ids_json }) => {
          if (f_match_event_ids_json) {
            //@ts-ignore
            //@ts-ignore
            f_match_event_ids_json.forEach(event => acc.push(event))
          }
          return acc
        },
        [],
      )

      if (!isEmpty(events)) {
        request({
          events,
          range: shootsRange,
          lang,
          lexical_formula,
          matchInfo: match,
          type: playListType,
        })
      }
    },
  }),
)

export default getExtLinkHOC
