import * as React from 'react'

type TProps = {
  //@ts-ignore
  tableStats?: Array<{}>,
  selectedParams: Array<{}>,
  getSortedList: (value: boolean) => (
    Array<{ f_match?: number, f_player?: number }>
  ),
  sortInc: boolean,
  pageId: string,
  activeId?: string,
  showOpponentStats?: boolean,
  Tr: any,
}

// Основной блок таблицы со статистикой
export const TBody = ({
  activeId,
  getSortedList,
  pageId,
  selectedParams,
  showOpponentStats,
  sortInc,
  tableStats,
  Tr,
}: TProps) => (
  <tbody>
    {getSortedList(sortInc)
      .map((trItem: { f_match?: number, f_player?: number }) => (
        <Tr
          key={trItem.f_player || trItem.f_match}
          rowInfo={trItem}
          pageId={pageId}
          selectedParams={selectedParams}
          tableStats={tableStats}
          activeId={activeId}
          showOpponentStats={showOpponentStats}
        />
      ))
    }
  </tbody>
)
