import { inject, observer } from 'mobx-react'

import { compose, withHandlers, withProps } from 'recompose'

import cx from 'classnames'
import get from 'lodash/get'
import find from 'lodash/find'

import { utils } from 'react-sport'

const PlayerTrComposed = compose(
  inject(
    'videoLink',
    'lexics',
    'shootsRange',
    'matchLinks',
    'teamPlayers',
    'teamProfile',
    'selectedMatches',
    'shootsRange',
  ),
  //@ts-ignore
  withProps(({ player: { f_player } }) => {
    const {
      helpers: { getLogo },
    } = utils
    const avatarUrl = getLogo({
      type: 'players_volleyball',
      id: f_player,
    })
    return { avatarUrl }
  }),
  //@ts-ignore
  withHandlers({
    handleClick: ({
      lexics: { lang, page },
      selectedMatches,
      shootsRange,
      teamPlayers: { fetchEvents },
      teamProfile: {
        profile: { id },
      },
      videoLink: { request },
    }) => (
      //@ts-ignore
      paramId,
      //@ts-ignore
      lexical_formula,
      //@ts-ignore
      playerId,
    ) => {
      fetchEvents(
        lang,
        lexical_formula,
        playerId,
        paramId,
        request,
        selectedMatches[page][id],
        shootsRange,
      )
    },
    /**
     * Получение класса для значения с % или обычных
     * @param {number} param_id - id параметра
     * @param {number} param1_id - id параметра необходимого для вычисления значения (проверка на %)
     * @param {number} gear_group_id - айди группы параметров
     * @param {number} activeId - активный id параметра (для отображения отсортированного)
     */
    getClassName: () => (
      {
        //@ts-ignore
        gear_group_id,
        //@ts-ignore
        param_id,
        //@ts-ignore
        param1_id,
      },
      //@ts-ignore
      activeId,
    ) =>
      cx({
        'clickable-cell': !param1_id,
        percents: param1_id,
        'sorted-by': `${param_id}_${gear_group_id}` === activeId,
      }),
    getParamValue: ({
      player: { params },
      teamPlayers: { tables },
      //@ts-ignore
      //@ts-ignore
    }) => ({ param_id }, position) => {
      const playerParam = find(params, { param: param_id })
      const value = get(tables, `${position}.summaryType`) === 'average_for_match'
        ? get(playerParam, 'averageValue')
        : get(playerParam, 'value')

      if (value) return value.toFixed()
      return '—'
    },
  }),
  observer,
)

export default PlayerTrComposed
