import React, { Fragment } from 'react'
import { inject, observer } from 'mobx-react'
import { withHandlers, compose } from 'recompose'
import cx from 'classnames'
import uniqBy from 'lodash/uniqBy'
import sortBy from 'lodash/sortBy'
import map from 'lodash/map'
import get from 'lodash/get'
import find from 'lodash/find'

import TeamsCompareStats from './TeamsCompareStats'
import TeamsCompareLogos from './TeamsCompareLogos'
import { TTeamsCompareInfo } from '../types'

export const TeamsCompareInfo = ({
  getClassName,
  getExtLink,
  getImage,
  getSortedStats,
  getStatValue,
  team1,
  team2,
}: TTeamsCompareInfo) => (
  <Fragment>
    <TeamsCompareLogos
      getImage={getImage}
      team1={team1}
      team2={team2}
    />
    <TeamsCompareStats
      stats={getSortedStats()}
      team1={team1}
      team2={team2}
      getStatValue={getStatValue}
      getClassName={getClassName}
      getExtLink={getExtLink}
    />
  </Fragment>
)

const TeamsCompareInfoComposed = compose(
  inject(
    'lexics',
    'videoLink',
    'shootsRange',
    'matchTable',
    'teamsCompareWidget',
  ),
  //@ts-ignore
  withHandlers({
    getSortedStats: ({
      teamsCompareWidget: {
        selectedParams,
        stats,
      },
    }) => () => {
      if (stats) {
        const uniqStats = uniqBy(stats, 'param')
        //@ts-ignore
        return sortBy(uniqStats, ({ param }) => {
          const selectedParamsIds = map(selectedParams, 'param_id')
          return selectedParamsIds.indexOf(param)
        })
      }
      return []
    },
    //@ts-ignore
    getImage: ({ teamsCompareWidget: { teamsInfo } }) => teamId => (
      get(teamsInfo, `${teamId}.photo_team`, '')
    ),
    //@ts-ignore
    getStatValue: ({ teamsCompareWidget: { teamsInfo } }) => (param, teamId) => {
      const value = get(
        find(teamsInfo[teamId].params, { param }),
        'value',
      )
      return value ? value.toFixed() : '-'
    },
    //@ts-ignore
    getClassName: ({ teamsCompareWidget: { teamsInfo } }) => (param, teamId) => {
      const hasVideo = get(
        find(teamsInfo[teamId].params, { param }),
        'f_match_event_ids',
      )
      return cx(
        't-compare__stats-num',
        { 't-compare__clickable-cell': hasVideo },
      )
    },
    getExtLink: ({
      lexics: { lang },
      shootsRange,
      teamsCompareWidget: { teamsInfo },
      videoLink: { request },
      //@ts-ignore
    }) => (param, param_name, teamId) => () => {
      //@ts-ignore
      const events: Array<any> = get(
        find(teamsInfo[teamId].params, { param }), 'f_match_event_ids',
      )
      if (events) {
        request({
          events,
          range: shootsRange,
          lang,
          lexical_formula: param_name,
          type: 'widgets',
          widgetType: 'teamsCompare',
        })
      }
    },
  }),
  observer,
  //@ts-ignore
)(TeamsCompareInfo)

export default TeamsCompareInfoComposed
